import React, { useState } from 'react';
import axios from 'axios';
import jwt_decode from 'jwt-decode';
import { useNavigate } from 'react-router-dom';
import './Auth.css';

interface LoginProps {
  onLogin: (token: string) => void; // 
}

const LoginAdmin: React.FC<LoginProps> = ({ onLogin }) => {
  const [username, setUsername] = useState(''); //  username
  const [password, setPassword] = useState(''); //  password
  const [error, setError] = useState<string | null>(null); // 
  const navigate = useNavigate(); // 

  const handleSubmit = async (event: React.FormEvent) => {
    event.preventDefault(); // 

    try {
      //  API 
      const response = await axios.post('http://localhost:3000/api/admin-login', { username, password });
      const token = response.data.token;

      //  token  localStorage
      localStorage.setItem('token', token);

      // 
      onLogin(token);

      // Decode token 
      const decodedToken: any = jwt_decode(token);
      console.log('Decoded Admin Token:', decodedToken);

      //  API 
      await axios.post(
        'http://localhost:3000/api/record-login',
        { admin_id: decodedToken.adminId || null }, //  admin_id  null
        {
          headers: { Authorization: `Bearer ${token}` }, //  token  Header
        }
      );

      // 
      navigate('/admin');
    } catch (error) {
      // 
      setError('Admin Login failed. Please check your username and password.');
    }
  };

  return (
    <div className="auth-background">
      <div className="auth-container">
        <h1 className="auth-title">Login Admin</h1>
        {/*  */}
        {error && <p style={{ color: 'red' }}>{error}</p>}
        <form onSubmit={handleSubmit}>
          <input
            type="text"
            placeholder="Enter username..."
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            className="login-input"
            required
          />
          <input
            type="password"
            placeholder="Enter password..."
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            className="login-input"
            required
          />
          <button type="submit" className="login-button">Login</button>
        </form>
        {/*  Sign Up */}
        <div className="register-link">
          Don't have an account? <a href="/registerAdmin">Sign up</a>
        </div>
      </div>
    </div>
  );
};

export default LoginAdmin;
